.card-see-more__title {
  margin-top: 15px;
  margin-bottom: 9px;

  .font-oswald();
  line-height: 20px;
  color: @dark-grey;
  text-transform: uppercase;
  text-align: center;
}

.card-see-more__text {
  margin: 0;
  margin-bottom: 20px;

  font-size: 12px;
  line-height: 16px;
  color: @special-dark-grey;
  text-align: center;
}

.card-see-more__button {
  width: 100%;
  padding: 8px;

  .font-oswald();
  text-transform: uppercase;

  border: none;
}
